import React, { useState } from "react";
import { Field, reduxForm } from "redux-form";
import { selectInput } from "../ReduxForm/form";
import {
  SEMESTER,
  YEAR,
  COURSE,
  CASTE,
  GENDER,
  BRANCH_BE,
  BRANCH_PDDC,
  BRANCH_PG,
} from "../Registration/data";

const SearchForm = ({ handleSubmit, submitting }) => {
  const [BRANCH, setBRANCH] = useState({ "Please Select Course First": "" });
  const onCourseChange = (e) => {
    switch (e.target.value) {
      case "BE":
        setBRANCH(BRANCH_BE);
        break;
      case "PDDC":
        setBRANCH(BRANCH_PDDC);
        break;
      case "PG":
        setBRANCH(BRANCH_PG);
        break;
      default:
        setBRANCH({ "Please Select Course First": "" });
        break;
    }
  };
  return (
    <form onSubmit={handleSubmit}>
      <div class="row">
        <div class="col-lg-4">
          <div class="form-group">
            <Field
              name="branch"
              label="Branch:"
              placeholder="Select Branch"
              component={selectInput}
              options={{ "All Branch": 0, ...BRANCH }}
              data-live-search="on"
            />
          </div>
        </div>
        <div class="col-lg-4">
          <div class="form-group">
            <Field
              name="semester"
              label="Semester:"
              placeholder="Select Semester"
              component={selectInput}
              options={{ "All SEM": 0, ...SEMESTER }}
              data-live-search="on"
            />
          </div>
        </div>
        <div class="col-lg-4">
          <div class="form-group">
            <Field
              name="addmission_year"
              label="Addmission Year:"
              placeholder="Select Addmission year"
              component={selectInput}
              options={{ "All Year": 0, ...YEAR }}
              data-live-search="on"
            />
          </div>
        </div>
        <div class="col-lg-4">
          <div class="form-group">
            <Field
              name="course"
              label="Course:"
              placeholder="Select Course"
              component={selectInput}
              options={{ "All Course": "ALL", ...COURSE }}
              onChange={onCourseChange}
              data-live-search="on"
            />
          </div>
        </div>
        <div class="col-lg-4">
          <div class="form-group">
            <Field
              name="caste"
              label="Caste:"
              placeholder="Select Caste"
              component={selectInput}
              options={{ "All Caste": "ALL", ...CASTE }}
              data-live-search="on"
            />
          </div>
        </div>
        <div class="col-lg-4">
          <div class="form-group">
            <Field
              name="gender"
              label="Gender:"
              placeholder="Select Gender"
              component={selectInput}
              options={{ "All Gender": "ALL", ...GENDER }}
              data-live-search="on"
            />
          </div>
        </div>
      </div>

      <div class="row mt-2">
        <div class="col-lg-4 form-group mx-auto">
          <button
            id="bth-search"
            class="btn btn-primary btn-lg btn-block"
            disabled={submitting}
          >
            Search
          </button>
        </div>
      </div>
    </form>
  );
};

export default reduxForm({ form: "searchForm" })(SearchForm);
